body {
  text-align: center;
  background: linear-gradient(#c67164, #45433c) fixed;
  font-family: Arial, Helvetica, sans-serif;
}

form {
  color: #f5f5f5;
  justify-content: center;
  justify-items: end;
  display: grid;
}

input {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  border: 2px solid #9294f8;
  border-radius: 5px;
  margin-top: 3rem;
  padding: .5rem;
  font-size: 1rem;
}

button {
  width: 200px;
  height: 40px;
  color: #000;
  text-transform: uppercase;
  border: 2px solid #9294f8;
  border-radius: 5px;
  margin-top: 3rem;
  margin-left: auto;
  font-size: 1rem;
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

button:hover {
  box-shadow: 1px 1px 25px 10px rgba(146, 148, 248, .4);
}

button:before {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(120deg, rgba(0, 0, 0, 0), rgba(146, 148, 248, .4), rgba(0, 0, 0, 0));
  transition: all .65s;
  position: absolute;
  top: 0;
  left: -100%;
}

button:hover:before {
  left: 100%;
}

